import global from "../../../utils/global.js";
import babyApi from "../../../service/baby";

export default {
	props:["nav","babyShow","babyList"],
	data() {
		return {
			statusBarHeight: 0, 
			titleBarHeight: 0, 
			isCheck: 1,
			show: false,
			radio: '0',
		}
	},
	created() {
		var  that = this;
		 	uni.getSystemInfo({
		 		success: function(res) {
		 			if (res.model.indexOf('iPhone') !== -1) {
						that.titleBarHeight = 44 + 'px';
		 			} else {
		 				that.titleBarHeight = 48  + 'px';
					}
						that.statusBarHeight = res.statusBarHeight  + 'px'
				},
					
		 	})
	 },
	onLoad(options) {
	  	if(global.getBabyId()){
	 		this.radio = global.getBabyId();
	 	}
	},
	methods: {
		onChange(event) {
		    this.radio = event.detail;
			global.setBabyId(this.radio);
			this.show = false;
			//点击刷新组件内页面
			// uni.$emit('refresh',true)
		},
		onClose() {
			this.show = false;
		},
		showPopup(type) {
			this.show = true;
			if(global.getBabyId()){
				this.radio = global.getBabyId();
			}
		},
		addBaby(){
			uni.navigateTo({
			    url:'/pages/baby/addBaby'
			});
		},
		tomyBaby(){
			uni.navigateTo({
			    url:'/pages/my/myBaby'
			});
		},
		 // 回到上一页
		back: function(){
		    uni.navigateBack({
				delta:1
		    })
		},
		setcheck(value){
			this.isCheck = value;
		},
		checkTab(value) {
			this.isCheck = value;
			this.$emit('changePage',this.isCheck)
		}
	}
}
